<script setup lang="ts">
import { onMounted, useModel } from "vue";
import clonedeep from "lodash.clonedeep";
import {
  ARow,
  ACol,
  ACheckbox,
  AContainer,
  ATextField,
} from "@actindo/core-ui";
import type { IDataTypeInstance } from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesStringValueConfiguration } from "./models";
import DatahubBadWordFilter from "./DatahubBadWordFilter.vue";

const props = defineProps<{
  modelValue?: IDataTypesStringValueConfiguration;
  dataTypeInstance: IDataTypeInstance<IDataTypesStringValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
}>();

defineEmits<{
  (event: "update:modelValue", value: IDataTypesStringValueConfiguration);
}>();

const configuration = useModel(props, "modelValue");

onMounted(() => {
  if (!configuration.value) {
    configuration.value = clonedeep(
      props.dataTypeInstance.defaultConfiguration,
    );
  }
});
</script>

<template>
  <AContainer>
    <ARow v-if="configuration">
      <ACol>
        <ATextField
          v-model="configuration.min"
          :label="$t('stringValue.minLength')"
        />
      </ACol>
      <ACol>
        <ATextField
          v-model="configuration.max"
          :label="$t('stringValue.maxLength')"
        />
      </ACol>
      <ACol>
        <ATextField
          v-model="configuration.regex"
          :label="$t('stringValue.regex')"
        />
      </ACol>
      <ACol>
        <ACheckbox
          v-model="configuration.readOnly"
          :label="$t('stringValue.readOnly')"
        />
      </ACol>
      <DatahubBadWordFilter
        v-model="configuration.badwords"
        :scopable="dataTypeInstance.scopable"
        :remote-scopes="remoteScopes"
        :multi-language="dataTypeInstance.multilanguage"
        :remote-languages="remoteLanguages"
      />
    </ARow>
  </AContainer>
</template>
